<template>
  <div class="row">
    <div class="col-sm-3">
      <div class="input-group input-group-sm">
        <div class="input-group-prepend">
          <label class="input-group-text" for="inputGroupSelect01">
            <i class="fa fa-cog"></i>
          </label>
        </div>
        <select class="custom-select" v-model="cog" @change="cogOption">
          <option value="0">-- 无操作 --</option>
          <option value="1">合并数量，并制作订货单</option>
        </select>
      </div>
    </div>
    <div class="col-sm-3">
    </div>
    <div class="col-sm-6 text-right">
      <pager-option :_page="_pageLink" @loadData_Father="_loadData"></pager-option>
    </div>

    <combine-listing-modal :isPrint="true" :combineList="combine_lc" ref="combineREF" @closed_Father="closedCombine"></combine-listing-modal>
  </div>
</template>

<script>
import view from '@/common/es/view'
import config from '@/common/config'
import tool from '@/common/es/tool'
import conn from '@/service/conn'
import conn_util from '@/common/es/conn_util'

import PagerOption from '@/components/Filter/PagerOption.vue'
import CombineListingModal from '@/components/Modal/Combine/CombineListingModal.vue'

export default {
  components: { 
    PagerOption,
    CombineListingModal
  },
  name: 'ListingViewFooter',
  props: { 
    _pageLink: {
      type: Object
    },
    _select: {
      type: Array
    }
  },
  data() {
    return {
      cog: 0,
      combine_lc: []
    }
  },
  methods: {
    _loadData(url) {
      this.$emit('_loadData_Father', url)
    },
    cogOption() {

      if (this.cog == 1) {
        if (this._select.length < 2) {
          tool.showToast('请选取多项后再尝试。')
          this.cog = 0
        } else {
          this.combineInvoice()
        }
      }
    },
    // 合并数量
    closedCombine() {
      this.cog = 0
    },
    _combine(lc) {
      if (this.combine_lc.length > 0) {
        const ids = this.combine_lc.map(e => e.id)
        
        for (const i of lc) {
          const index = tool.indexList(i.id, ids)
          
          if (index < 0) {
            this.combine_lc.push(i)
          } else {
            const item = this.combine_lc[index]
            item.count = tool.floatNum.floatAdd(item.count, i.count)
            this.combine_lc[index] = item
          }
        }
      } else {
        this.combine_lc = lc
      }
    },
    combineInvoice() {
      this.combine_lc = []

      const _this = this

      const _call = function(res) {
        if (conn.judge_response_list(res)) {

          const lc = res[0].content
          if (lc.length > 1) {
            let _lc = JSON.parse(lc)
            _lc = _lc.map( e => {
              return {
                id: e.id,
                num: e.num,
                named: e.named,
                unit: e.unit,
                count: e.count
              }
            })

            _this._combine(_lc)
          }
        }
      }

      let url = conn.listing_content.url
      
      for (const id of this._select) {
        tool.net.get( url + conn_util.buildURL({ listing: id }, true), false, _call)
      }
      console.log(this.combine_lc)

      tool.showModal('combineListingModal')
      this.$refs.combineREF.locationData(this.combine_lc)
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/content.sass"

</style>


