<template>
  <div class="of-visible">
    <div class="row of-visible">
      <div class="col-sm-6 pt-2">
        <h5 class="fw-none">
          全部
        </h5>
      </div>
      <div class="col-sm-6 pt-1 text-right of-visible">

        <button class="btn btn-info btn-sm pb-0 float-right" @change="sortOption">
          <i class="fa fa-toggle-down" v-if="(_ordering.length > 8)"></i>
          <i class="fa fa-toggle-up" v-else></i>
        </button>

        <search-option @openSearch_Father="openSearch"></search-option>

      </div>
    </div>
    
    <table class="table m-0">
      <tr>
        <td width="6%" class="text-accent">

          <div class="form-check hand">
            <input class="form-check-input position-static" type="checkbox" v-model="isAll" @click="fullChoise">
          </div>
        </td>
        <td width="10%">发票单号</td>
        <td width="20%">发票日期</td>
        <td width="10%">总价</td>
        <td width="10%">报价单</td>
        <td width="20%">客户</td>
        <td width="12%" class="text-center">Pdf 文件</td>
        
        <td width="12%" class="text-center"></td>
      </tr>
    </table>

  </div>
</template>

<script>
import view from '@/common/es/view'
import config from '@/common/config'
import tool from '@/common/es/tool'
import conn from '@/service/conn'
import HelpIcon from '@/components/Modal/Help/HelpIcon.vue'
import SearchOption from '@/components/Filter/SearchOption.vue'

export default {
  components: { HelpIcon, SearchOption },
  props: {
    _condition: {
      type: Object
    },
    _ordering: {
      type: String
    },
    _select: {
      type: Array
    },
    _listFull: {
      type: Array
    }
  },
  name: 'ListingViewTop',
  data() {
    return {
      isAll: false
    }
  },
  methods: {
    openSearch() { tool.showModal('listingFilterSearchModal') },
    sortOption() {
      if (this._ordering.length > 8) { this.$emit('sortOption_Father', 'add_time') } else { this.$emit('sortOption_Father', '-add_time') }
    },

    fullChoise() {
      setTimeout(() => {
        this._select.splice(0, this._select.length)
        if (this.isAll) {
          this._listFull.map( e => this._select.push(e.id) )
        }
      }, 100)
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

</style>


