<template>
  <div class="modal fade" tabindex="-1" role="dialog" id="combineListingModal" data-backdrop="static" data-keyboard="false">
    <div class="modal-dialog modal-dialog-centered modal-dialog-scrollable">
      <div class="modal-content">

        <div class="modal-header">
          <h4 class="modal-title">
            订货单
            <div class="fs-12">
              提示: 关闭该弹出框，已编辑的内容都会作废，请谨慎操作。
            </div>
          </h4>
          <div class="btn btn-light float-right pb-0" @click="closeSure">
            <i class="fa fa-close"></i>
          </div>
        </div>
        
        <div class="modal-body">

          <table class="table table-top-line-none text-left" v-if="resList.length > 0">
            <tr>
              <td width="12%">#</td>
              <td width="18%">货号</td>
              <td width="30%">描述</td>
              <td width="25%" class="text-center">
                数量
                <i class="fa fa-pencil text-primary" v-if="!isEdit" @click="editOption(true)"></i>
                <i class="fa fa-close text-danger" v-else @click="editOption(false)"></i>
              </td>
              <td width="15%" class="text-center">单位</td>
            </tr>

            <tr v-for="(v, index) in resList" :key="v.id">
              <td>{{ index + 1 }}</td>
              <td>
                {{ v.num }}
              </td>
              <td>{{ v.named }}</td>

              <td class="text-center table-input-td">
                <div class="input-group input-group-sm listing-num-input">
                  <input 
                    type="text" 
                    class="form-control text-center " 
                    :disabled="!isEdit"
                    :id="'combine_count_' + v.id" 
                    :value="v.count" 
                    @keyup.enter="changeCount(v.id, index)"
                    @change="changeCount(v.id, index)"
                  >
                </div>
              </td>
              <td class="text-center">{{ unit(v.unit) }}</td>
            </tr>
          </table>
          <div class="loading-wrapper" v-if="resList.length < 1">
            <loading-middle></loading-middle>
          </div>
        </div>
        
        <div class="modal-footer fs-12">
          <button class="btn btn-outline-info btn-sm print-btn" v-if="isPrint" @click="refreshSure">导出 Pdf</button>
        </div>

      </div>
    </div>

    <custom-modal :id_prefix="`combineSure`" :centered="false"
      :title="`提示`" 
      :txt="`你确定要关闭吗？`"
      @done_Father="closed"
    >
    <div slot="submit_text">确定</div>
    </custom-modal>

    <custom-modal :id_prefix="`combinePrintSure`" :centered="false"
      :title="`提示`" 
      :txt="`
        你确定要<span class='text-primary'>导出 Pdf</span> 吗？
      `"
      @done_Father="printPdf"
    >
    <div slot="submit_text">确定</div>
    </custom-modal>
  </div>
</template>

<script>
import view from '@/common/es/view'
import config from '@/common/config'
import tool from '@/common/es/tool'
import conn from '@/service/conn'
import conn_util from '@/common/es/conn_util'
import LoadingMiddle from '../../Shimmer/LoadingMiddle.vue'
import CustomModal from '../CustomModal.vue'

export default {
  components: { 
    LoadingMiddle,
    CustomModal 
  },
  name: 'CombineListingModal',
  props: {
    isPrint: {
      type: Boolean,
      default: false
    },
    combineList: {
      type: Array
    }
  }, 
  data() {
    return {
      isEdit: false,
      resList: []
    }
  },
  methods: {
    unit (u) {
      return view.ser_unit(u)
    },
    locationData(v) {
      this.resList = v
    },
    editOption(v) {
      this.isEdit = v
      if (v) {
        tool.showToast('数量编辑功能已打开。')
      } else {
        tool.showToast('编辑功能已关闭。')
      }
    },
    changeCount(id, index) {
      this.resList[index].count = tool.value('combine_count_' + id)
    },

    // 确认打印
    refreshSure() {
      tool.showModal('combinePrintSureModal')
    },
    printPdf() {
      console.log(this.resList)
    },
    
    // 确认关闭
    closeSure() {
      tool.showModal('combineSureModal')
    },
    closed() {
      this.$emit('closed_Father')
      setTimeout(() => {

        tool.closeAllModal()
      }, 600)
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/pdf.sass"
@import "@/common/sass/base.sass"
@import "@/common/sass/choise.sass"

.loading-wrapper
  height: 40vh
  
.modal-head-table
  display: block !important
</style>


