<template>
  <full-height-layout>
    <div slot="layout_title">发票</div>

    <listing-view-top 
      slot="layout_inner_head" 
      :_select="select"
      :_ordering="ordering" 
      :_condition="condition" 
      :_listFull="listingList"  

      @loadData_Father="loadData"
      @sortOption_Father="sortOption"  
    ></listing-view-top>
  
    <listing-view-body 
    
      :_listingList="listingList"  
      slot="layout_inner_body"
      @option_Father="option"

      @pdfView_Father="pdfView"
      
      v-if="listingList.length > 0"

      :_select="select"
    >
    </listing-view-body>

    <loading v-else-if="isLoading" slot="layout_inner_body"></loading>
    <empty v-else slot="layout_inner_body"></empty>
    

    <listing-view-footer slot="layout_footer" 
      :_select="select"
      :_pageLink="pageLink" 
      @_loadData_Father="loadData"
    ></listing-view-footer>

    <!-- 其它插件 -->
    <div slot="layout_extra">

      <trash @trashItem="_trash"></trash>
      
      <listing-pdf-modal
        :_numed="activeNum"
        :_timed="activeTimed"
        :_payment="activePayment"
        :membery="activeMembery"
        :_listing_content="activeListingContent"
        :_activeId="activeId"

        :isPlus="false"
        :isPrint="true"

      ></listing-pdf-modal>

      <listing-filter-search @loadData_Father="loadData"></listing-filter-search>

    </div>
  </full-height-layout>
</template>

<script>
import view from '@/common/es/view'
import config from '@/common/config'
import tool from '@/common/es/tool'
import conn from '@/service/conn'
import conn_util from '@/common/es/conn_util'

import ListingViewTop from './Top/ListingViewTop.vue'
import FullHeightLayout from '@/components/Layout/FullHeightLayout.vue'
import ListingViewBody from './Body/ListingViewBody.vue'

import Trash from '@/components/Modal/Trash.vue'
import ListingPdfModal from '@/components/Modal/Pdf/ListingPdfModal.vue'
import Empty from '@/components/Shimmer/Empty.vue'
import Loading from '@/components/Shimmer/Loading.vue'
import ListingFilterSearch from './Filter/ListingFilterSearch.vue'
import ListingViewFooter from './Footer/ListingViewFooter.vue'

export default {
  components: { 
    ListingViewTop,
    FullHeightLayout,
    ListingViewBody,
    Trash,
    ListingPdfModal,
    Empty,
    Loading,
    ListingFilterSearch,
    ListingViewFooter
  },
  name: 'ListingView',
  data() {
    return {
      listingList: [],
      select: [],
      isLoading: true,
      
      ordering: '-add_time',
      condition: { 
        search: { }
      },
      activeId: 0,

      activeMembery: null,
      activeListingContent: null,
      activeNum: '',
      activeTimed: '',
      activePayment: 0,

      pageLink: {
        next: '',
        previous: '',
        count: 0,
        offsetNow: 0,
        limit: 100
      }
    }
  },
  mounted() {
    this.loadData()
  },
  methods: {
    // 初始化
    loadData(url, condition = {}) {
      if (url == null) {
        url = conn_util.buildFirstPagerUrl(conn.listing.url, condition, { offset: 0, limit: this.pageLink.limit })
      }
      
      setTimeout(() => {
        conn.listing.getListPage(url).then(res => {
          this.listingList = res.results
          this.pageLink = conn_util.buildPageLink(url, res.count, 100, res.next, res.previous)

          this.isLoading = false
        })
      }, config.delayTime)
    },
    //
    pdfView(listing_content) {
      const listing = listing_content.listing
      
      this.activeMembery = listing.membery
      
      this.activeMembery.contact_named = listing.pay_contact_named
      this.activeMembery.area.id = listing.pay_contact_area
      this.activeMembery.phoned = listing.pay_contact_phone
      this.activeMembery.email = listing.pay_contact_email

      this.activeListingContent = JSON.parse(listing_content.content)
      this.activeNum = listing.num
      this.activeTimed = tool.timed.ser_timed(listing.listing_time, true, true)
      this.activePayment = listing.pay_way
      this.activeId = listing.id
      
      tool.showModal('listingPdfViewModal')
    },
    // 过滤
    sortOption(v) {
      this.ordering = v
    },
    // 数据操作
    option(id, option) {
      this.activeId = id

      if (option == 'update') {

        conn.listing_content.getOneByListing({
          listing: id
        }).then(res => {
          
          if (conn.judge_response_list(res)) {
            
            this.$router.push({
              path: '/home/listing_update',
              query: {
                listingContentId: res[0].id
              }
            })
            
          } else {
            tool.showToast('该数据已过期！！！')
          }
        })
      } else if (option == 'trash') {
        tool.showModal('trashModal')
      }
    },
    _trash() {
      const _this = this
      tool.closeModal('trashModal')

      if ((this.activeId != null) && (this.activeId > 0)) {

        conn.listing.trash(this.activeId).then(res => {
          if (conn.judge_response(res)) {

            const id = res['id']
            tool.showToast('删除成功！！')
            _this.loadData()
            _this.isLoading = false
          }
        })

      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

</style>


