<template>
  <div class="modal fade" id="listingFilterSearchModal" data-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
    <div class="modal-dialog modal-dialog-centered">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="staticBackdropLabel">
            批量搜索
            <help-icon :help_code="'listing_search'"></help-icon>
          </h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          
          <form class="form">
            <div class="form-group">
              <label for="listing_num">发票编号</label>
              <div class="input-group">
                <input type="text" class="form-control" id="listing_num" placeholder="请输入发票编号" v-model="listing_num">
                <div class="input-group-append">
                  <span class="input-group-text" id="basic-addon2" @click="listing_num = ''">清空</span>
                </div>
              </div>
            </div>
            
            <div class="form-group">
              <label for="prices_num">报价单号</label>
              <div class="input-group">
                <input type="text" class="form-control" id="prices_num" placeholder="请输入报价单单号" v-model="prices_num">
                <div class="input-group-append">
                  <span class="input-group-text" id="basic-addon2" @click="prices_num = ''">清空</span>
                </div>
              </div>
            </div>
            
            <div class="form-group">
              <label for="membery_num">客户编号</label>
              <div class="input-group">
                <input type="text" class="form-control" id="membery_num" placeholder="请输入客户编号" v-model="membery_num">
                <div class="input-group-append">
                  <span class="input-group-text" id="basic-addon2" @click="membery_num = ''">清空</span>
                </div>
              </div>
            </div>

            <div class="form-group">
              <label for="membery_named">客户名称</label>
              <div class="input-group">
                <input type="text" class="form-control" id="membery_named" placeholder="请输入客户名称" v-model="membery_named">
                <div class="input-group-append">
                  <span class="input-group-text" id="basic-addon2" @click="membery_named = ''">清空</span>
                </div>
              </div>
            </div>
          </form>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
          <button type="button" class="btn btn-main" @click="sureSubmit">确认搜索</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HelpIcon from '@/components/Modal/Help/HelpIcon.vue'
export default {
  components: { HelpIcon },
  name: 'ListingFilterSearch',
  data() {
    return {
      listing_num: '',
      prices_num: '',
      membery_num: '',
      membery_named: '',

      search_f: false
    }
  },
  methods: {
    validate() {

      this.prices_num = this.prices_num.trim()
      this.listing_num = this.listing_num.trim()
      this.membery_num = this.membery_num.trim()
      this.membery_named = this.membery_named.trim()

      if ((this.prices_num == '') && 
        (this.listing_num == '') && 
        (this.membery_num == '') && 
        (this.membery_named == '')
      ) { return false }

      const res = { }

      if (this.listing_num != '') {
        res.num = this.listing_num
      }
      if (this.prices_num != '') {
        res.price_num = this.prices_num
      }
      if (this.membery_num != '') {
        res.membery_num = this.membery_num
      }
      if (this.membery_named != '') {
        res.membery_named = this.membery_named
      }
      
      return res
    },
    sureSubmit() {
      const data = {
        prices_num: this.prices_num,
        listing_num: this.listing_num,
        membery_num: this.membery_num,
        membery_named: this.membery_named
      }
      
      const res = this.validate()
      if (res == false) { return false }
      
      const condition = { 
        search_f: 'True',
        ...res
      }
      this.$emit('loadData_Father', null, condition)
      this.search_f = true
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
</style>


